

.navcontainer{
  text-align: center;
  height: 200px;
}

.nav {
  list-style-type: none;
  display: inline-block;
  margin: 0px;
  padding: 0px;
  background: linear-gradient(90deg, rgba(56,87,156,1) 0%, rgba(108,62,150,1) 100%);
  height: 100%;
  width: 100%;
}
.nav:hover{
  background: #333333;
}

.navitem{
  color: #ffffff;
  display: inline;
  padding: 0px;
}

.navitem img {
  height: 100%;
}

#navitem1:hover img{
  background: linear-gradient(90deg, rgba(56,87,156,1) 0%, rgba(66,82,163,1) 100%);
}

#navitem2:hover img{
  background: linear-gradient(90deg, rgba(66,82,163,1) 0%, rgba(77,77,160,1) 100%);
}

#navitem3:hover img{
  background: linear-gradient(90deg, rgba(77,77,160,1) 0%, rgba(87,72,156,1) 100%);
}

#navitem4:hover img{
  background: linear-gradient(90deg, rgba(87,72,156,1) 0%, rgba(97,67,153,1) 100%);
}

#navitem5:hover img{
  background: linear-gradient(90deg, rgba(97,67,153,1) 0%, rgba(108,62,150,1) 100%);
}

.myCard {
  border-radius: 0px 20px 0px 20px;
  filter: drop-shadow(0 0 0.3rem grey);
  border: 0px;
}

.myCard:hover {
  filter: drop-shadow(0 0 0.4rem grey);
}

.myCardGroup{
  background: linear-gradient(90deg, rgba(56,87,156,1) 0%, rgba(108,62,150,1) 100%);
  border: 0px;
}

.myCardGroup:hover{
  background: #333333;
  border: 0px;
}

.myCardGroupCard{
  background: transparent;
  border: 0px;
}

.myCardGroupImage{
  height: 100%;
}

#inactive1, #inactive2, #inactive3, #inactive4, #inactive5, #inactive6, #inactive7, #inactive8, #inactive9{
  background: #333333;
}

#myCardGroupImage1:hover, #inactive1:hover{
  background: linear-gradient(90deg, rgba(56,87,156,1) 0%, rgba(66,82,163,1) 100%);
}

#myCardGroupImage2:hover, #inactive2:hover{
  background: linear-gradient(90deg, rgba(66,82,163,1) 0%, rgba(77,77,160,1) 100%);
}

#myCardGroupImage3:hover, #inactive3:hover {
  background: linear-gradient(90deg, rgba(77,77,160,1) 0%, rgba(87,72,156,1) 100%);
}

#myCardGroupImage4:hover, #inactive4:hover {
  background: linear-gradient(90deg, rgba(87,72,156,1) 0%, rgba(97,67,153,1) 100%);
}

#myCardGroupImage5:hover, #inactive5:hover {
  background: linear-gradient(90deg, rgba(97,67,153,1) 0%, rgba(108,62,150,1) 100%);
}

#myCardGroupImage6:hover, #inactive6:hover{
  background: linear-gradient(90deg, rgba(56,87,156,1) 0%, rgba(70,80,154,1) 100%);
}

#myCardGroupImage7:hover, #inactive7:hover {
  background: linear-gradient(90deg, rgba(70,80,154,1) 0%, rgba(82,74,153,1) 100%);
}

#myCardGroupImage8:hover, #inactive8:hover {
  background: linear-gradient(90deg, rgba(82,74,153,1) 0%, rgba(95,68,151,1) 100%);
}

#myCardGroupImage9:hover, #inactive9:hover {
  background: linear-gradient(90deg, rgba(95,68,151,1) 0%, rgba(108,62,150,1) 100%);
}





