.supernavitem {
  display: inline-block;
  text-align: center;
  width: 33%;
  padding: .75rem 0;
  margin: 0;
  text-decoration: none;
  color: #333;
  font-size: 18pt;
}

.supernavcontainer{
  cursor: pointer;
  padding-left: 0px;
}

.one:hover ~ hr{
  margin-left: 0%;
}

.two:hover ~ hr {
  margin-left: 33%;
}

.three:hover ~ hr{
  background: #333333;
  margin-left: 66%;
}

.hr1 {
  height: .25rem;
  width: 33%;
  margin-left: 0%;
  margin-top: 0;
  background: #333333;
  border: none;
  transition: .3s ease-in-out;
}

.hr2 {
  height: .25rem;
